<template>
  <div class="login">
    <h1 class="title">SFA</h1>
    <div class="login-con-wrap">
      <div class="login-con-hat"></div>
      <div class="login-main-con">
        <div class="logo-wrap">
          <div class="logo"></div>
        </div>
        <div class="login-input-group">
          <!-- class- active  class active_index===1 -->
          <!-- errors.has('comNum')namecomNuminputtrue -->
          <div
            class="login-input-field"
            :class="{
              active: active_index === 1,
              error: errors.has('comNum')
            }"
          >
            <label for="comNum">:</label>
            <input
              id="comNum"
              ref="comNum"
              v-model="company_num"
              v-validate="'required|length:4'"
              type="number"
              placeholder="4"
              name="comNum"
              @focus="
                active_index = 1
                $refs.comNum.placeholder = ''
              "
              @blur="$refs.comNum.placeholder = '4'"
            />
            <!--  -->
            <!-- <span>{{ errors.first('comNum') }}</span> -->
          </div>
          <div
            class="login-input-field"
            :class="{
              active: active_index === 2,
              error: errors.has('perNum')
            }"
          >
            <label for="perNum">:</label>
            <input
              id="perNum"
              ref="perNum"
              v-model="person_num"
              v-validate="{ required: true, length: 4 }"
              type="number"
              placeholder="4"
              name="perNum"
              @focus="
                active_index = 2
                $refs.perNum.placeholder = ''
              "
              @blur="$refs.perNum.placeholder = '4'"
            />
          </div>
          <div
            class="login-input-field"
            :class="{ active: active_index === 3, error: errors.has('pwd') }"
          >
            <label for="pwd">:</label>
            <input
              id="pwd"
              ref="pwd"
              v-model="password"
              v-validate="'required|min:6'"
              type="password"
              placeholder="6"
              name="pwd"
              @focus="
                active_index = 3
                $refs.pwd.placeholder = ''
              "
              @blur="$refs.pwd.placeholder = '6'"
            />
          </div>
        </div>
        <div class="login-option-wrap">
          <div class="remember-pwd" @click="changeRememberStatus">
            <i
              class="iconfont"
              :class="remember ? 'icon-check-square' : 'icon-border'"
            ></i>
            <span></span>
          </div>
          <div class="auto-login" @click="changeAutologStatus">
            <i
              class="iconfont"
              :class="autolog ? 'icon-check-square' : 'icon-border'"
            ></i>
            <span></span>
          </div>
        </div>
      </div>
    </div>
    <div class="login-btn" @click="handleLogBtnClick">
      <p></p>
    </div>
  </div>
</template>

<script>
import '../assets/font/iconfont.css'
import { Indicator, Toast } from 'mint-ui'
import { mapMutations, mapState } from 'vuex'
// axiosservice
import service from '../service'
export default {
  name: 'Login',
  components: {},
  data() {
    return {
      // active_indexinputactiveclass
      active_index: 0,
      company_num: '',
      person_num: '',
      password: '',
      remember: false,
      autolog: false
    }
  },
  computed: {
    ...mapState(['LoginUser'])
  },
  created() {
    // localstoragedata
    let data = JSON.parse(localStorage.getItem('Login_data'))
    for (let i in data) {
      this[i] = data[i]
    }
    // homestopAutoLog
    if (this.autolog === true && !this.$route.query.stopAutoLog) {
      this.handleLogBtnClick()
    }
    // 
    if (this.$route.params.PNO) this.person_num = this.$route.params.PNO
    if (this.$route.params.CNO) this.company_num = this.$route.params.CNO
  },
  methods: {
    ...mapMutations([
      //  `this.saveUserData()`  `this.$store.commit('saveUserData')`
      'saveUserData',
      'cleartUserData'
    ]),
    // 
    changeRememberStatus() {
      this.remember = !this.remember
      // rememberfalseautologfalse
      this.remember || (this.autolog = false)
    },
    changeAutologStatus() {
      this.autolog = !this.autolog
      this.autolog && (this.remember = true)
    },
    async handleLogBtnClick() {
      // await
      await this.$validator.validate()
      // this.errors.any()--errorstrue
      if (this.errors.any()) {
        Toast({
          message: '',
          duration: 1000
        })
      } else {
        // log
        this.cleartUserData()
        localStorage.clear()
        sessionStorage.clear()

        Indicator.open('...')
        service
          .login({
            CNO: this.company_num,
            PNO: this.person_num,
            Passwd: this.password
          })
          .then(res => {
            console.log(res)
            Indicator.close()
            if (res.data.code === 1) {
              // 
              // sessionStorage,,.,
              if (this.remember) {
                this.setDataToLocalStorage()
              }
              // sessionStoragevuex
              ///////////////////////////////////////////
              res.data.user.CNO = this.company_num
              res.data.user.PNO = this.person_num
              //////////////////////////////////////////////
              this.setDataToSessionStorage(res)
              // vuex
              this.setDataToVuex(res)
              // home
              this.$router.push('/home')
            } else {
              // 
              Toast({
                message: '',
                duration: 1000
              })
            }
          })
          .catch(e => {
            console.log(e)
            Indicator.close()
            Toast({
              message: '',
              duration: 1000
            })
          })
      }
    },
    // localStoragejsonJSON.stringify(data)JSON.parse()json
    setDataToLocalStorage() {
      localStorage.setItem(
        'Login_data',
        JSON.stringify({
          remember: this.remember,
          autolog: this.autolog,
          company_num: this.remember ? this.company_num : '',
          person_num: this.remember ? this.person_num : '',
          password: this.remember ? this.password : ''
        })
      )
    },
    setDataToSessionStorage(res) {
      sessionStorage.setItem('Login_data', JSON.stringify(res.data.user))
      sessionStorage.setItem('LoginToken', JSON.stringify(res.data.token))
    },
    setDataToVuex(res) {
      this.saveUserData(res.data.user)
    }
  }
}
</script>

<style lang="scss" scoped>
.login {
  overflow-x: hidden;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #2ade69 !important;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.title {
  color: #fff;
  font-size: px2rem(36);
  text-align: center;
  // padding-top: px2rem(148-40-36);
}
.login-con-wrap {
  margin-top: px2rem(40);
  .login-con-hat {
    width: px2rem(537);
    height: px2rem(18);
    background-color: #eeeeee;
    margin: 0 auto;
    border-radius: px2rem(18) px2rem(18) 0 0;
  }
  .login-main-con {
    width: px2rem(600);
    height: px2rem(736);
    background-color: #fff;
    margin: 0 auto;
    border-radius: px2rem(14);
    .logo-wrap {
      height: px2rem(150);
      padding-top: px2rem(50);
      .logo {
        width: px2rem(150);
        height: px2rem(150);
        background-image: url(../assets/logo.png);
        background-size: cover;
        margin: 0 auto;
      }
    }
    .login-input-group {
      margin-top: px2rem(10);
      .login-input-field {
        width: px2rem(440);
        height: px2rem(90);
        line-height: px2rem(90);
        padding-left: px2rem(30);
        margin: 0 auto;
        font-size: px2rem(28);
        color: #757575;
        border: 2px solid #e2e2e2;
        border-radius: px2rem(40);
        margin-top: px2rem(30);
        input {
          width: px2rem(250);
          padding-left: px2rem(25);
          border: 0 none;
          outline: 0 none;
          font-size: px2rem(28);
        }
        label {
          font-weight: bold;
        }
      }
    }
    .login-input-field.active {
      border: 2px solid #10903d;
      color: #10903d;
    }
    .login-input-field.error {
      border: 2px solid red;
      color: red;
    }
    .login-option-wrap {
      // margin-top: px2rem(45);
      font-size: px2rem(24);
      color: #757575;
      span {
        margin-left: px2rem(15);
        letter-spacing: px2rem(2);
        font-size: px2rem(30);
      }
      i.iconfont {
        font-size: px2rem(30);
      }
      .remember-pwd {
        display: inline-block;
        padding: px2rem(40) 0 px2rem(40) px2rem(90);
      }
      .remember-pwd.active,
      .auto-login.active {
        color: #10903d;
      }
      .auto-login {
        display: inline-block;
        padding: px2rem(40) 0 px2rem(40) px2rem(80);
      }
    }
  }
}
.login-btn {
  width: px2rem(600);
  height: px2rem(100);
  line-height: px2rem(100);
  text-align: center;
  margin: px2rem(20) auto 0;
  color: #10903d;
  background-color: #fff;
  font-size: px2rem(36);
  border-radius: px2rem(15);
  letter-spacing: px2rem(5);
}

// placeHolder
input::-webkit-input-placeholder {
  color: #ccc;
  font-size: px2rem(25);
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #ccc;
  font-size: px2rem(25);
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #ccc;
  font-size: px2rem(25);
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #ccc;
  font-size: px2rem(25);
}
</style>
