<template>
  <!-- Count -->
  <!-- 
    :
    max

    <Count v-model="count" :max="111"></Count> 

    data() {
      return {
        count: 1
      }
    }
  -->

  <div class="count">
    <div class="reduce" @click="countReduce"><div>-</div></div>
    <!--  valuevalue,0
       this.$refs.input.value==''emit0,inputvalue0value0'' -->
    <input
      ref="input"
      :value="value == 0 ? '' : value"
      type="text"
      @input="inputInput"
      @blur="inputBlur"
    />
    <div class="add" @click="countAdd"><div>+</div></div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui'
export default {
  name: 'Count',
  props: {
    value: { type: Number, default: 1 },
    max: { type: Number, default: 1 }
  },
  data() {
    return {}
  },
  methods: {
    countAdd() {
      if (this.$refs.input.value >= this.max) {
        Toast({
          message: `${this.max}`,
          position: 'middle',
          duration: 1000
        })
        return
      }
      // valueemitinputvaluevaluepropsinputvalue
      this.$emit('input', ++this.$refs.input.value)
    },
    countReduce() {
      if (this.$refs.input.value <= 1) {
        Toast({
          message: '~',
          position: 'middle',
          duration: 1000
        })
        return
      }
      this.$emit('input', --this.$refs.input.value)
    },
    inputBlur() {
      if (this.$refs.input.value == '') {
        Toast({
          message: '1~',
          position: 'middle',
          duration: 1000
        })
        this.$refs.input.value = 1
      }
      this.$emit('input', Number(this.$refs.input.value))
    },
    inputInput() {
      // input
      this.$refs.input.value = this.$refs.input.value.replace(
        /^(0+)|[^\d]+/g,
        ''
      )

      console.log(this.$refs.input.value)
      // if (this.$refs.input.value == '') {
      //   return
      // }
      if (this.$refs.input.value > this.max) {
        Toast({
          message: `${this.max}`,
          position: 'middle',
          duration: 1000
        })
        this.$refs.input.value = this.max
      }
      this.$emit('input', Number(this.$refs.input.value))
    }
  }
}
</script>

<style lang="scss" scoped>
