//     height: px2rem(47);
//     // padding: px2rem(28);
//   }
//   input {
//     border: 1px solid black;
//     width: px2rem(90);
//     height: px2rem(47);
//     text-align: center;
//     border-radius: 5px;
//   }
// }

.count {
  display: flex;
  justify-content: space-between;
  align-items: center;
  // width: px2rem(220);
  text-align: center;
  .add,
  .reduce {
    padding: px2rem(18);
    div {
      border: 1px solid black;
      width: px2rem(47);
      height: px2rem(47);
    }
  }
  .add {
    // padding-right: px2rem(18);
  }
  input {
    border: 1px solid black;
    width: px2rem(90);
    height: px2rem(47);
    text-align: center;
    border-radius: 5px;
  }
}
</style>
