<template>
  <div class="view">
    <TopHead title="" class="header">
      <i slot="r" class="iconfont" @click="submitSigin">&#xe61e;</i>
    </TopHead>
    <div class="main">
      <ul class="picWrap">
        <li>
          <ImgBtn
            icon-class="icon-xiangji"
            val=""
            @btnClick="addPhoto"
          ></ImgBtn>
        </li>
        <li
          v-for="item in imgList"
          :key="item.url"
          class="item"
          @click="item.isChecked = !item.isChecked"
        >
          <div v-if="item.isChecked" class="mask">
            <i class="iconfont">&#xe61e;</i>
          </div>
          <img :src="item.url" />
        </li>
        <li>
          <!-- isred -->
          <ImgBtn
            icon-class="icon-shanchudange"
            val=""
            :isred="true"
            @btnClick="delPhoto"
          ></ImgBtn>
        </li>
      </ul>
      <!-- // css -->
      <input ref="inputFile" type="file" class="file" @change="filesChange" />
      <p class="warn">
        <span></span><br />
        
      </p>
    </div>
  </div>
</template>

<script>
import ImgBtn from '../components/ImgBtn'
import service from '../service'
// import canvasResize from 'canvas-resize'
// import Utility from '../common/Utility'
