export default {
  name: 'Sign',
  components: {
    ImgBtn
  },
  data() {
    return {
      imgList: []
    }
  },
  created() {
    // 
  },
  mounted() {},
  methods: {
    addPhoto() {
      // <input type="file">click
      this.$refs.inputFile.click()
    },
    delPhoto() {
      this.imgList = this.imgList.filter(item => item.isChecked == false)
      // valuevalue
      this.$refs.inputFile.value = ''
    },
    // 
    filesChange() {
      this.imgList.push({
        isChecked: false,
        url: `https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1573725886876&di=3261d3dc3e2b02578512b4ed6413ca9f&imgtype=0&src=http%3A%2F%2Fimg3.qianzhan.com%2Fnews%2F201404%2F28%2F20140428-809d810e3e374304_980x660.jpg`
      })

      // let formData = new FormData()
      // // 
      // let file = this.$refs.inputFile.files[0]
      // filecallbackbase64
      // canvasResize(file, {
      //   crop: false, // 
      //   quality: 0.5, //  0-1
      //   rotate: 0, // 
      //   callback: baseStr => {
      //     // base64 Blob()
      //     let fileBlob = Utility.convertBase64UrlToBlob(baseStr, {
      //       type: file.type
      //     })
      //     // name imgF;
      //     formData.append('imgF', fileBlob)
      //     service
      //       .uploadImg(formData)
      //       .then(res => {
      //         console.log(res)
      //         // this.imgList.push(`http://localhost:8889${res.data.img}`)
      //         // http://localhost:8889.envVUE_APP_BASEURL=http://localhost:8889.envvue_cli--
      //         this.imgList.push({
      //           isChecked: false,
      //           url: `${process.env.VUE_APP_BASEURL}${res.data.img}`
      //         })
      //         // inputvalue
      //         this.$refs.inputFile.value = ''
      //         // console.log(this.imgList)
      //       })
      //       .catch(e => {
      //         console.log('uploadImg failed')
      //         console.log(e)
      //       })
      //   }
      // })
    },
    // 
    submitSigin() {
      console.log(this.imgList)
      if (this.imgList.length == 0) {
        Toast({
          message: '',
          position: 'middle',
          duration: 2000
        })
        return
      }
      service
        .submitSigin({
          id: Date.now(),
          shopId: this.$route.params.id,
          SubOn: new Date().toLocaleDateString(),
          imgs: this.imgList.map(item => item.url)
        })
        .then(res => {
          console.log(res)
          Toast({
            message: '',
            position: 'middle',
            duration: 500
          })
          setTimeout(() => {
            this.$router.go(-1)
          }, 500)
        })
        .catch(e => {
          console.log(e)
          Toast({
            message: '',
            position: 'middle',
            duration: 500
          })
        })
    }
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header {
    // TopHeadflexflex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    overflow: scroll;
  }
}

.header {
  i {
    font-size: px2rem(36);
  }
}

.picWrap {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  padding: px2rem(20);
  .item {
    img {
      width: px2rem(210);
      height: px2rem(210);
    }
  }
  li {
    position: relative;
    margin: 0 px2rem(20) px2rem(20) 0;
    .mask {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: white;
      opacity: 0.6;
      i {
        border-radius: 50%;
        background: #06c;
        position: absolute;
        top: px2rem(5);
        right: px2rem(5);
        width: px2rem(40);
        height: px2rem(40);
        font-size: px2rem(30);
        text-align: center;
        color: white;
      }
    }
  }
}

.file {
  display: none;
}

.warn {
  padding: px2rem(20);
  margin-top: px2rem(50);
  span {
    color: red;
  }
}
</style>
