<template>
  <!-- Search -->
  <!--  -->
  <!-- 
    // v-model="q" :
    //   :value="q"
    //   @input="input"       
    //   method:{
    //     input(){
    //         q=$event.target.value
    //     }
    //   }
    // 
    // input$event.target.valueq
    //  
    // qvalueSearchprops
    
    <Search
      v-model="q"
      class="search"
      @inputInput="inputInput"
      @inputFocus="inputFocus"
      @inputBlur="inputBlur"
      @inputClear="inputClear" 
      @inputChange="inputChange"
      @keyupEnter="keyupEnter"
      @handleSearch="handleSearch"
    ></Search>  

    methods: {
    inputInput(value) {
      console.log('inputInput')
    },  
    inputFocus() {
      console.log('inputFocus')
    },
    inputBlur() {
      console.log('inputBlur')
    },
    inputClear() {
      console.log('inputClear')
    },
    // inputvaluekeyupEnter/blur 
    inputChange() {
      console.log('inputChange')
    },
    handleSearch() {
      console.log('handleSearch')
    },
    // inputvalue
    inputValChange() {
      console.log('inputValChange')
    },
    keyupEnter() {
      console.log('keyupEnter')
    }
  }
  -->

  <div class="searchWrapper">
    <div class="wrapperL">
      <i class="iconfont">&#xe615;</i>
      <!-- 
        
          @input="$emit('input', $event.target.value)
          inputinputinput$event.target.valueinputvalue

        
          :value="value"
          inputvaluevalue
      -->
      <input
        ref="input"
        :value="value"
        type="text"
        placeholder=""
        @input="
          $emit('input', $event.target.value)
          $emit('inputInput')
          inputInput()
        "
        @focus="
          inputFocus()
          $emit('inputFocus')
        "
        @blur="
          inputBlur()
          $emit('inputBlur')
        "
        @change="$emit('inputChange')"
        @keyup.enter="
          $emit('handleSearch')
          $emit('keyupEnter')
        "
      />
    </div>
    <div v-show="inputActive" class="wrapperR">
      <i
        class="iconfont cancel"
        @click="
          inputClear()
          $emit('inputClear')
        "
        >&#xe63a;</i
      >
      <div class="line"></div>
      <div class="searchText" @click="$emit('handleSearch')">
        
      </div>
    </div>
    <!-- <div v-show="!inputActive" class="fnWrapper"></div> -->
  </div>
</template>

<script>
export default {
  name: 'VisitShop',
  props: {
    //  valueinputvalue
    value: { type: String, default: '' }
  },
  data() {
    return {
      inputActive: false
    }
  },
  methods: {
    inputFocus() {
      this.$refs.input.placeholder = ''
    },
    inputBlur() {
      this.$refs.input.placeholder = ''
    },
    inputClear() {
      this.$emit('input', '')
      this.inputActive = false
    },
    inputInput() {
      if (this.$refs.input.value) {
        this.inputActive = true
      } else {
        this.inputActive = false
        this.$emit('inputClear')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.searchWrapper {
  border: 2px solid #ccc;
  height: px2rem(80);
  // background: #fafafa;
  width: 94%;
  margin-left: 3%;
  border-radius: px2rem(10);
  overflow: hidden;
  display: flex;
  .wrapperL {
    flex: 1;
    display: flex;
    i {
      font-size: px2rem(40);
      width: px2rem(80);
      height: 100%;
      line-height: px2rem(80);
      text-align: center;
      color: #ccc;
      // background: red;
    }
    input {
      flex: 1;
      height: 100%;
      line-height: px2rem(80);
      border: none;
      font-size: px2rem(30);
      // background: #fafafa;
      // background: orange;
    }
  }
  .wrapperR {
    display: flex;
    align-items: center;
    .searchText {
      width: px2rem(100);
      text-align: center;
      line-height: px2rem(80);
      font-size: px2rem(30);
      color: black;
      // background: yellow;
    }
    .line {
      height: 50%;
      border: 1px solid #ccc;
    }
    .cancel {
      font-size: px2rem(30);
      height: 100%;
      line-height: px2rem(80);
      width: px2rem(80);
      text-align: center;
      color: #ccc;
      // background: pink;
    }
  }

  // placeHolder
  input::-webkit-input-placeholder {
    color: #ccc;
  }
  input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ccc;
  }
  input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ccc;
  }
  input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #ccc;
  }
}
</style>
