<template>
  <div class="notice view">
    <TopHead title="" class="header"></TopHead>
    <div class="main">
      <!--mescroll-->
      <mescroll-vue
        ref="mescroll"
        :down="mescrollDown"
        :up="mescrollUp"
        class="mescroll"
        @init="mescrollInit"
      >
        <ul class="noticeList">
          <li
            v-for="(item, index) in noticeList"
            :key="index"
            class="noticeItem"
          >
            <router-link :to="`/notice/${index}`">
              <div class="noticeWrap">
                <i
                  class="icon iconfont icon-dian"
                  :class="{ readed: item.isRead }"
                ></i>
                <div class="notice-bd">
                  <h3 class="noticeTitle" :class="{ readed: item.isRead }">
                    {{ item.title }}
                  </h3>
                  <p class="noticeDate" :class="{ readed: item.isRead }">
                    {{ item.SubDate }}
                  </p>
                </div>
              </div>
            </router-link>
          </li>
        </ul>
      </mescroll-vue>
    </div>
  </div>
</template>

<script>
import '../assets/font/iconfont.css'
import TopHead from '../components/TopHead'
import service from '../service'
import MescrollVue from 'mescroll.js/mescroll.vue'
import { mapState } from 'vuex'
import { mapMutations } from 'vuex'
export default {
  name: 'Notice',
  components: {
    TopHead,
    MescrollVue
  },
  data() {
    return {
      latestDate: new Date(),
      earlistDate: new Date(),
      mescroll: null, // mescroll
      mescrollDown: {
        auto: false,
        callback: this.downCallback
      }, //. (,mescrollDown)
      mescrollUp: {
        // .
        auto: true,
        callback: this.upCallback, // ,;  callback: function(page, mescroll) { }
        htmlNodata: '<p class="upwarp-nodata">--  --</p>',
        toTop: {
          //
          src: require('@/assets/img/mescroll-totop.png'), //,null,
          offset: 1000 //1000px
        }
      },
      // mescroll
      hasNext: true
    }
  },
  computed: {
    ...mapState({
      noticeList: 'noticeList'
    })
  },
  created() {},
  activated() {
    // keepAlive
    console.log(this.noticeList.length)
    if (this.noticeList.length === 0) {
      console.log('00000')
      this.mescroll.triggerUpScroll()
    }
  },
  beforeRouteEnter(to, from, next) {
    console.log('beforeRouteEnter')
    // isBounce,beforeRouteEnter
    next(vm => {
      // mescrollref,mescroll-vuebeforeRouteEnter
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter() // ,,isBounce
    })
  },
  beforeRouteLeave(to, from, next) {
    console.log('beforeRouteLeave')
    // isBounce,beforeRouteLeave
    // mescrollref,mescroll-vuebeforeRouteLeave
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave() // ,,isBounce
    next()
  },
  methods: {
    ...mapMutations(['noticeListPush', 'noticeListUnshift']),
    mescrollInit(mescroll) {
      this.mescroll = mescroll // this.mescroll,mescrollInit
    },
    upCallback(page, mescroll) {
      console.log('upCallback')
      service
        .getNotice(this.latestDate, 10, false)
        .then(res => {
          this.noticeListPush(res.data.data.messages.reverse())
          // latestDateeearlistDate
          this.latestDate = new Date(this.noticeList[0].SubDate)
          if (this.noticeList.length > 200) {
            this.hasNext = false
          }
          // ,,mescroll.endSuccess,
          this.$nextTick(() => {
            mescroll.endSuccess(res.data.data.messages.length, this.hasNext)
          })
        })
        .catch(() => {
          // ,;
          mescroll.endErr()
        })
    },
    downCallback(mescroll) {
      console.log('downCallback')
      console.log(this.earlistDate)
      service
        .getNotice(this.earlistDate, 10, true)
        .then(res => {
          // 
          if (res.data.data.messages <= 0) {
            return
          }
          this.noticeListUnshift(res.data.data.messages.reverse())
          // earlistDateearlistDate
          this.earlistDate = new Date(
            this.noticeList[this.noticeList.length - 1].SubDate
          )
          this.$nextTick(() => {
            mescroll.endSuccess()
          })
        })
        .catch(e => {
          // ,;
          mescroll.endErr()
          console.log(e)
        })
    }
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header {
    // TopHeadflexflex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    // overflow: scroll;
  }
}

.mescroll {
  position: fixed;
  top: px2rem(120);
  bottom: 0;
  height: auto;
}

.noticeList {
  background: white;
  .noticeItem {
    height: px2rem(120);
    padding: px2rem(50) 0 0 px2rem(40);
    border-bottom: px2rem(1) solid #ccc;
    .noticeTitle {
      font-size: px2rem(30);
      color: #000;
      line-height: px2rem(28);
    }
    .noticeDate {
      font-size: px2rem(26);
      color: #666;
      line-height: px2rem(16+34);
    }
    .noticeWrap {
      display: flex;
      i {
        flex: 0 0 px2rem(43);
        color: red;
        line-height: px2rem(20);
        font-size: px2rem(50);
      }
      .readed {
        color: #ccc;
      }
    }
  }
}
</style>
