<template>
  <div class="order view">
    <TopHead title="" class="header">
      <div slot="r" @click="toCart">
        <i class="icon iconfont icon-cart"></i>
      </div>
    </TopHead>
    <Search
      v-model="q"
      class="search"
      @handleSearch="handleSearch"
      @inputClear="inputClear"
    ></Search>
    <!--  -->
    <div class="nav-wrap">
      <ul>
        <li :class="{ act: isOnSales }" @click="isOnSalesStatus"></li>
        <li :class="{ act: isAll }" @click="isAllStatus"></li>
        <li style="color:#ccc">XXXXX</li>
        <li style="color:#ccc">XXXXX</li>
        <li style="color:#ccc">XXXXX</li>
      </ul>
    </div>
    <!--   -->
    <div class="goods-header">
      <div class="fl count-wrap">{{ cur }} / {{ total }}</div>
      <div class="fr">
        <ul class="type-list-wrap" @click="showSelectGoodsType = true">
          <li>{{ getFilterGoodsType }}</li>
          <li><i class="icon iconfont icon-filter"></i></li>
        </ul>
      </div>
    </div>
    <div id="main" ref="main" class="goods-list-wrap main">
      <mescroll-vue ref="mescroll" :up="mescrollUp" @init="mescrollInit">
        <ul class="goods-list">
          <li v-for="item in goods" :key="item.id">
            <router-link :to="`/goodsselect/${item.id}`">
              <GoodsListItem
                :number-visible="true"
                :goods="item"
              ></GoodsListItem>
            </router-link>
          </li>
        </ul>
      </mescroll-vue>
    </div>
    <popup v-model="showSelectGoodsType">
      <Checklist
        v-model="filterGoodsType"
        title=""
        :options="['', '', '']"
      >
      </Checklist>
      <Button
        type="default"
        class="btn-select-type"
        @click="showSelectGoodsType = false"
      >
        
      </Button>
    </popup>
  </div>
</template>

<script>
import Search from '../components/Search'
import service from '../service'
import GoodsListItem from '../components/GoodsListItem'
import { Popup, Checklist, Button, Toast, Indicator } from 'mint-ui'
import MescrollVue from 'mescroll.js/mescroll.vue'
import { mapState, mapMutations } from 'vuex'

export default {
  name: 'Order',
  components: {
    Search,
    GoodsListItem,
    Popup,
    Checklist,
    Button,
    MescrollVue
  },
  data() {
    return {
      showSelectGoodsType: false,
      q: '',
      isOnSales: false, // goods
      isAll: true, //
      total: 0,
      cur: 0,
      filterGoodsType: ['', '', ''],
      loading: false,

      // mescroll
      mescroll: null, // mescroll
      mescrollUp: {
        // .
        auto: false,
        callback: this.upCallback, // ,;  callback: function(page, mescroll) { }
        //,.
        page: {
          num: 0, // 0,1; callback(page)1
          size: 15 //,10
        },
        htmlNodata: '<p class="upwarp-nodata">--  --</p>',
        noMoreSize: 5, //,5;
        // // (),
        // // 
        toTop: {
          //
          src: require('@/assets/img/mescroll-totop.png'), //,null,
          offset: 1000 //1000px
        },
        empty: {
          //,; warpId
          warpId: 'main', //id (1.3.5dom)
          // icon: './static/mescroll/mescroll-empty.png', //,null,
          tip: '' //
        }
      },
      hasNext: true
    }
  },
  computed: {
    ...mapState(['goods', 'curOrderShop', 'cartList']),
    getFilterGoodsType() {
      return this.filterGoodsType.join('/')
    }
  },
  created() {},
  mounted() {
    // mescrolltop ,
    this.setMescroll()
    this.getData()
  },
  methods: {
    ...mapMutations(['goodsPush', 'emptyGoods']),
    handleSearch() {
      this.getData()
    },
    // 
    inputClear() {
      this.getData()
    },
    getData() {
      // mescroll0
      this.emptyGoods()
      this.mescrollUp.page.num = 0
      this.hasNext = true
      // ,this.upCallbackaxiosupCallback
      this.mescroll.triggerUpScroll()
    },
    isOnSalesStatus() {
      this.isOnSales = true
      this.isAll = false
      this.getData()
    },
    isAllStatus() {
      this.isOnSales = false
      this.isAll = true
      this.getData()
    },
    // mescroll,mescroll
    mescrollInit(mescroll) {
      this.mescroll = mescroll // this.mescroll,mescrollInit
    },
    //  page = {num:1, size:10}; num: ,1; size:,10
    upCallback(page, mescroll) {
      console.log('upCallback')
      Indicator.open('...')

      // isOnSalesparamsonsalestrueonsales
      let params = { q: this.q, _page: page.num, _limit: page.size }
      if (this.isOnSales) {
        params.onsales = this.isOnSales
      }
      service
        .getGoods(params)
        .then(res => {
          console.log(res)
          Indicator.close()

          // yapi///////////////
          if (this.q.length >= 4) {
            res.data = []
          }
          /////////////////////////////////////////////////////////////////////

          this.goodsPush(res.data)
          this.total = res.headers['x-total-count']

          //  yapithis.total/////////////////////
          this.total = res.headers['x-total-count']
            ? res.headers['x-total-count']
            : this.isOnSales
            ? 159
            : 320
          //////////////////////////////////////////////////////////////////

          if (this.total <= page.size) {
            this.cur = this.total
          } else {
            this.cur = page.num * page.size
            if (this.cur >= this.total) {
              this.cur = this.total
            }
          }
          if (!this.isOnSales && this.goods.length >= 320) {
            this.hasNext = false
          }
          if (this.isOnSales && this.goods.length >= 159) {
            this.hasNext = false
          }
          // ,,mescroll.endSuccess,
          this.$nextTick(() => {
            mescroll.endSuccess(res.data.length, this.hasNext)
          })
        })
        .catch(e => {
          console.log(e)
          Indicator.close()
          Toast('')
          // ,;
          mescroll.endErr()
        })
    },
    setMescroll() {
      // mescrolltop
      this.$refs.mescroll.$el.style.top = this.$refs.main.offsetTop + 'px'
      // mescroll 
      this.mescroll.lockDownScroll(true)
    },
    toCart() {
      // 
      let shop = this.cartList.find(
        item => this.curOrderShop.id == item.shop.id
      )
      if (!shop || shop.warehouseArray.length === 0) {
        Toast({
          message: '~',
          position: 'middle',
          duration: 1000
        })
        return
      }
      this.$router.push('/cart/00')
    }
  },
  beforeRouteEnter(to, from, next) {
    // isBounce,beforeRouteEnter
    next(vm => {
      // mescrollref,mescroll-vuebeforeRouteEnter
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter() // ,,isBounce
    })
  },
  beforeRouteLeave(to, from, next) {
    // isBounce,beforeRouteLeave
    // mescrollref,mescroll-vuebeforeRouteLeave
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave() // ,,isBounce
    next()
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header,
  .search,
  .goods-header {
    // TopHeadflexflex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    // overflow: scroll;
  }
}
.mescroll {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  height: auto;
}
.search {
  margin-top: px2rem(20);
  margin-bottom: px2rem(20);
}
.nav-wrap {
  background-color: #fafafa;
  padding: px2rem(14) px2rem(28);
  font-size: px2rem(30);
  border: px2rem(1) solid #ccc;
  border-left: none;
  border-right: none;
  ul {
    display: flex;
    align-items: center;
    height: px2rem(58);
    line-height: px2rem(58);
    text-align: center;
    li {
      height: px2rem(68);
      flex: 1;
      border-right: px2rem(1) solid #ccc;
      line-height: px2rem(68);
      // background-color: pink;
    }
    li:last-child {
      border-right: none;
    }
    .act {
      color: #04afeb;
    }
  }
}
.goods-header {
  margin: 0 px2rem(28);
  // padding-right: px2rem(28);
  overflow: hidden;
  border-bottom: 1px solid #ccc;
  .count-wrap,
  .type-list-wrap {
    height: px2rem(84);
    font-size: px2rem(30);
    line-height: px2rem(84);
  }
  .type-list-wrap {
    display: flex;
    li {
      flex: 0 0 px2rem(58px);
      i {
        font-size: px2rem(30);
      }
    }
  }
}
.goods-list-wrap {
  padding-top: -px2rem(28);
  .goods-list {
    padding: 0 px2rem(28);
  }
}
.btn-select-type {
  margin-top: px2rem(10);
  width: px2rem(140);
  height: px2rem(60);
  font-size: px2rem(25);
}
</style>
