<template>
  <div class="visitShop view">
    <TopHead title="" class="header"></TopHead>
    <Tab class="tab" @leftClick="actived = 1" @rightClick="actived = 2">
      <template v-slot:titleL
        >
      </template>
      <template v-slot:titleR>
        
      </template>
    </Tab>
    <div v-show="actived == 1" class="wrap">
      <Search
        v-model="q"
        class="search"
        @handleSearch="handleSearch"
        @inputClear="inputClear"
      ></Search>
      <div id="main" ref="main" class="main">
        <mescroll-vue
          ref="mescroll"
          :up="mescrollUp"
          class="mescroll"
          @init="mescrollInit"
        >
          <ul class="listWrap">
            <router-link
              v-for="item in shopList"
              :key="item.id"
              :to="`/visitshop/${item.id}`"
              class="listItem"
            >
              <ul>
                <li class="top">
                  <span>{{ item.name }}</span>
                  <span>
                    <i class="iconfont">&#xe61f;</i>&lt;{{ item.distance }}
                  </span>
                </li>
                <li class="middle">{{ `id:${item.id}` }}</li>
                <li class="bottom">{{ item.bossName }}</li>
              </ul>
              <i class="iconfont iR">&#xe84e;</i>
            </router-link>
          </ul>
        </mescroll-vue>
      </div>
    </div>
    <div v-show="actived == 2" class="main right">
      <div></div>
    </div>
  </div>
</template>

<script>
import Tab from '../components/Tab'
import Search from '../components/Search'
import service from '../service'
import { Indicator, Toast } from 'mint-ui'
import MescrollVue from 'mescroll.js/mescroll.vue'
import { mapState, mapMutations } from 'vuex'
// import Utility from '../common/Utility'
export default {
  name: 'VisitShop',
  components: {
    Tab,
    MescrollVue,
    Search
  },
  data() {
    return {
      inputActive: false,
      q: '',
      pos: null,
      // mescroll
      mescroll: null, // mescroll
      mescrollUp: {
        // .
        auto: false,
        callback: this.upCallback, // ,;  callback: function(page, mescroll) { }
        //,.
        page: {
          num: 0, // 0,1; callback(page)1
          size: 10 //,10
        },
        htmlNodata: '<p class="upwarp-nodata">--  --</p>',
        noMoreSize: 5, //,5;
        // // (),
        // // 
        toTop: {
          //
          src: require('@/assets/img/mescroll-totop.png'), //,null,
          offset: 1000 //1000px
        },
        empty: {
          //,; warpId
          warpId: 'main', //id (1.3.5dom)
          // icon: './static/mescroll/mescroll-empty.png', //,null,
          tip: '' //
        }
      },
      hasNext: true,
      actived: 1
    }
  },
  computed: {
    ...mapState({
      shopList: 'shopList'
    })
  },
  created() {},
  mounted() {
    // shopgetLocationpromise
    // Utility.getLocation()
    //   .then(pos => {
    //     this.pos = pos
    //     this.getData()
    //   })
    //   .catch(e => {
    //     console.log(e)
    //   })
    ////////////////////////////////////////////////
    Indicator.open('...')
    setTimeout(() => {
      console.log('sett')
      Indicator.close()
      this.pos = { Lng: 11, Lat: 11 }
      this.getData()
    }, 1000)

    ///////////////////////////////////////////////////
    // mescrolltop ,
    this.setMescroll()
  },
  activated() {
    // keepAlive
    if (this.shopList.length === 0) {
      // shopgetLocationpromise
      // Utility.getLocation()
      //   .then(pos => {
      //     this.pos = pos
      //     this.getData()
      //   })
      //   .catch(e => {
      //     console.log(e)
      //   })
      ////////////////////////////////////////////////
      Indicator.open('...')
      setTimeout(() => {
        console.log('sett')
        Indicator.close()
        this.pos = { Lng: 11, Lat: 11 }
        this.getData()
      }, 1000)
      ///////////////////////////////////////////////////
      // mescrolltop ,
      this.setMescroll()
    }
  },
  deactivated() {
    console.log('deactivated')
    Indicator.close()
  },
  methods: {
    ...mapMutations(['shopListPush', 'emptyShopList']),
    // 
    handleSearch() {
      this.getData()
    },
    // 
    inputClear() {
      this.getData()
    },
    getData() {
      // possearch
      if (!this.pos) {
        Toast('')
        return
      }
      // mescroll0
      this.emptyShopList()
      this.mescrollUp.page.num = 0
      this.hasNext = true
      // ,this.upCallbackaxiosupCallback
      this.mescroll.triggerUpScroll()
    },
    setMescroll() {
      // mescrolltop
      this.$refs.mescroll.$el.style.top = this.$refs.main.offsetTop + 'px'
      // mescroll 
      this.mescroll.lockDownScroll(true)
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll // this.mescroll,mescrollInit
    },
    upCallback(page, mescroll) {
      console.log('upCallback')
      Indicator.open('...')
      service
        .getShops(this.pos.Lng, this.pos.Lat, this.q, page.num)
        // .getShops(1, 1, this.q, page.num)
        .then(res => {
          console.log(res)
          Indicator.close()

          // 
          if (this.q.length >= 3) {
            res.data = []
          }
          /////////////////////////////////////////////////////////////////////

          this.shopListPush(res.data)
          if (res.data.length <= 0 || this.shopList.length >= 60) {
            this.hasNext = false
          }
          // ,,mescroll.endSuccess,
          this.$nextTick(() => {
            mescroll.endSuccess(res.data.length, this.hasNext)
          })
        })
        .catch(e => {
          console.log(e)
          Indicator.close()
          Toast('')
          // ,;
          mescroll.endErr()
        })
    }
  },
  beforeRouteEnter(to, from, next) {
    // isBounce,beforeRouteEnter
    next(vm => {
      // mescrollref,mescroll-vuebeforeRouteEnter
      vm.$refs.mescroll && vm.$refs.mescroll.beforeRouteEnter() // ,,isBounce
    })
  },
  beforeRouteLeave(to, from, next) {
    // isBounce,beforeRouteLeave
    // mescrollref,mescroll-vuebeforeRouteLeave
    this.$refs.mescroll && this.$refs.mescroll.beforeRouteLeave() // ,,isBounce
    next()
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header,
  .tab,
  .search {
    // flex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    // overflow-y: scroll;
    margin-top: px2rem(20);
  }
}
.search {
  margin-top: px2rem(20);
}
.mescroll {
  position: fixed;
  // top: 0;
  bottom: 0;
  height: auto;
  // z-index: 99;
}
.listWrap {
  .listItem {
    height: px2rem(150);
    margin: 0 5%;
    padding: px2rem(15) 0;
    border-bottom: 1px solid #ccc;
    display: flex;
    align-items: center;
    // background: pink;
    ul {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width: 90%;
      .top {
        height: px2rem(30);
        line-height: px2rem(30);
        font-size: px2rem(30);
        // background: pink;
        display: flex;
        justify-content: space-between;
        i {
          font-size: px2rem(30);
        }
      }
      .middle {
        height: px2rem(25);
        line-height: px2rem(25);
        font-size: px2rem(25);
        // background: pink;
      }
      .bottom {
        height: px2rem(25);
        line-height: px2rem(25);
        font-size: px2rem(25);
        // background: pink;
      }
    }
    .iR {
      font-size: px2rem(30);
      // background: pink;
      width: 10%;
      text-align: right;
    }
  }
}

.right {
  text-align: center;
  padding-top: px2rem(40);
  color: gray;
}
</style>
