<template>
  <div class="noticeDetail view">
    <TopHead title="" class="header"></TopHead>
    <div class="main">
      <!-- // axiosmountedmountednoticenotice.title,mountednotice.title -->
      <h3>{{ notice ? notice.title : '' }}</h3>
      <p>{{ notice ? notice.text : '' }}</p>
    </div>
  </div>
</template>

<script>
import TopHead from '../components/TopHead'
import { mapState } from 'vuex'
import { mapMutations } from 'vuex'
import service from '../service'
export default {
  name: 'NoticeDetail',
  components: {
    TopHead
  },
  data() {
    return {
      notice: null
    }
  },
  computed: {
    ...mapState(['noticeList'])
  },
  mounted() {
    const noticeIndex = this.$route.params.id
    this.notice = this.noticeList[noticeIndex]

    // 
    service.setNoticeReaded(noticeIndex)
    // vuexnoticeList
    this.setNoticeReaded(noticeIndex)
  },
  methods: {
    ...mapMutations(['setNoticeReaded'])
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header {
    // TopHeadflexflex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    overflow-y: scroll;
  }
}

.main {
  padding: px2rem(20);
  h3 {
    line-height: px2rem(35);
    font-weight: bold;
  }
  p {
    margin-top: px2rem(15);
    line-height: px2rem(30);
    text-indent: px2rem(60);
  }
}
</style>
