import { Indicator, Toast } from 'mint-ui'

export default {
  /**
   * base64urlBlob
   * @param urlData String   urlbase64
   * @param type Object { type: "image/png" }
   */
  convertBase64UrlToBlob(urlData, type) {
    var bytes = window.atob(urlData.split(',')[1]) //urlbyte
    //,ascii00
    var ab = new ArrayBuffer(bytes.length)
    var ia = new Uint8Array(ab)
    for (var i = 0; i < bytes.length; i++) {
      ia[i] = bytes.charCodeAt(i)
    }
    return new Blob([ab], type)
  },

  // promise
  /* eslint-disable */
  getLocation(href) {

    Indicator.open('...')
    return new Promise((resolve, reject) => {
      var geolocation = new window.qq.maps.Geolocation(
        '65DBZ-RNYK6-KRWS3-ESEJR-LL5VZ-NCBKQ',
        'myapp'
      )
      geolocation.getLocation(
        position => {
          Indicator.close()
          console.log(position)
          resolve(position)
        },
        () => {

          Indicator.close()
          reject()
          console.log('')
          // Toast
          if (window.location.href != href) {
            return
          }
          Toast('')

        }
      )
    })
  }
  /* eslint-disable */
}