<template>
  <div class="view">
    <TopHead title="" class="header">
      <div slot="r" @click="confirmClick"></div>
    </TopHead>
    <div class="main">
      <div ref="map" class="map"></div>

      <ul class="wrap">
        <li class="address">{{ address }}</li>
        <li class="name">
          <div></div>
          <input ref="name" type="text" placeholder="" />
        </li>
        <li class="area">
          <div></div>
          <input ref="area" type="text" placeholder="" />
        </li>
        <li class="leader">
          <div></div>
          <input ref="leader" type="leader" placeholder="" />
        </li>
        <li class="tel">
          <div></div>
          <input ref="tel" type="text" placeholder="" />
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Utility from '../common/Utility'
import imgl from '../assets/img/marker.png'
import { Indicator, Toast, MessageBox } from 'mint-ui'
export default {
  name: 'AddShop',
  data() {
    return {
      address: ''
    }
  },
  created() {
    // if (this.isWeiXin() && this.isAndroid()) {
    //   MessageBox(
    //     '',
    //     '7.0httpsios'
    //   )
    // }
  },
  mounted() {
    this.getMap()
  },
  destroyed() {
    console.log('destroyed')
    Indicator.close()
  },
  methods: {
    getMap() {
      //  getLocationpromise
      let href = window.location.href
      Utility.getLocation(href)
        .then(pos => {
          this.initMap(pos)
        })
        .catch(e => {
          console.log(e)
        })
    },
    /* eslint-disable */
    initMap(pos) {
      // 
      let center = new qq.maps.LatLng(pos.lat, pos.lng)
      // 
      var map = new qq.maps.Map(this.$refs.map, {
        center,
        zoom: 13, // 
        mapStyleId: 'style1' // keystyle1
      })
      // 
      var marker = new qq.maps.Marker({
        position: center,
        map: map
      })
      var anchor = new qq.maps.Point(0, 39),
        size = new qq.maps.Size(42, 68),
        origin = new qq.maps.Point(0, 0),
        markerIcon = new qq.maps.MarkerImage(imgl, size, origin, anchor)
      marker.setIcon(markerIcon)

      // api
      let geocoder = new qq.maps.Geocoder()
      // 
      geocoder.setComplete(result => {
        // map.setCenter(result.detail.location);
        // 
        marker.setPosition(result.detail.location) // 
        this.address = result.detail.address // 
      })

      //  
      geocoder.getAddress(center)

      qq.maps.event.addListener(map, 'click', event => {
        center = new qq.maps.LatLng(
          event.latLng.getLat(),
          event.latLng.getLng()
        )
        // map.setCenter(center);
        marker.setPosition(center)
        geocoder.getAddress(center)
      })
    },
    /* eslint-disable */
    confirmClick() {
      // 
      if (
        !this.$refs.name.value ||
        !this.$refs.area.value ||
        !this.$refs.leader.value ||
        !this.$refs.tel.value
      ) {
        Toast({
          message: '',
          position: 'middle',
          duration: 1000
        })
        return
      }
      // axios
      Toast({
        message: '',
        position: 'middle',
        duration: 500
      })
      setTimeout(() => {
        this.$router.go(-1)
      }, 500)
    },
    isWeiXin() {
      var ua = window.navigator.userAgent.toLowerCase()
      if (ua.match(/MicroMessenger/i) == 'micromessenger') {
        return true
      } else {
        return false
      }
    },
    isAndroid() {
      let u = navigator.userAgent
      let isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1
      if (isAndroid) {
        return true
      } else {
        return false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
// headermain
.view {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 100%;
  height: 100%;
  .header {
    // TopHeadflexflex-shrink1head0
    flex-shrink: 0;
  }
  .main {
    overflow-y: scroll;
  }
}

.map {
  width: 100%;
  height: px2rem(500);
}

.wrap {
  .address {
    padding: 0 px2rem(40);
    background-color: #fafafa;
  }
  li {
    height: px2rem(100);
    display: flex;
    align-items: center;
    border-bottom: 1px solid #ccc;
    div {
      width: px2rem(160);
      padding-left: px2rem(40);
    }
    input {
      height: px2rem(50);
      padding: px2rem(20) 0;
      flex: 1;
      border: none;
      padding-right: px2rem(40);
    }
  }

  .active {
    color: red;
    font-weight: bold;
  }

  // placeHolder
  input::-webkit-input-placeholder {
    color: #ccc;
  }
  input::-moz-placeholder {
    /* Mozilla Firefox 19+ */
    color: #ccc;
  }
  input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18 */
    color: #ccc;
  }
  input:-ms-input-placeholder {
    /* Internet Explorer 10-11 */
    color: #ccc;
  }
}

.warn {
}
</style>
